@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: 'Dosis', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  margin: 0px;
  padding: 0px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
  min-height: 100vh;
background-image: linear-gradient(120deg, #a6c0fe 0%, #f68084 100%);
  padding: 50px 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.app__searchForm{
  margin-top: 20px;
}
.app__input{
  padding: 6px 16px;
  min-width: 200px;
  max-width: 100vw;
  font-size: 16px;
}
.app__button{
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 3px;
  background-color: #477fc4;
  border: 2px solid #475cc4;
  border-radius: 4px;
  color: white;
  cursor: pointer;
}
.app__button:hover{
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 3px;
  background-color: white;
  border: 2px solid #;
  border-radius: 4px;
  color: #02aff0;
  cursor: pointer;
}

.app__recipes{
  margin-top:24px;
  display: flex;
  grid-template-columns: 230px 230px;
  align-items: baseline;
  flex-wrap: wrap;
}

.app__healthLabel{
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 3px;
  border: 1px solid black;
  border-radius: 4px;
  /* color: black; */
}

.recipeTile{
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0px 5px 20px rgb(71,71,71);
  margin: 20px;
  flex-direction: column;
  display: flex;
  background: white;
  justify-content: space-around;
  align-items: center;
  min-width: 40%;
  background-image: linear-gradient(-225deg, #E3FDF5 0%, #FFE6FA 100%);
}

.recipeTile__img{
  padding-top: 7px;
  height: 250px;
  width: 250px;
  object-fit: contain;
  border-radius: 4px;
}

.recipeTile__name{
  max-width: 200px;
  text-align: center;
  margin: 12px 0px;
}

.ingredients{
  text-align: center;
  font-size: 18px;
}

